






































































import { Component, Vue } from 'vue-property-decorator'
import TableList from '@/components/TableList/index.vue'
import Pagination from '@/components/Pagination/index.vue'
import { fetchList, deleteData, fetchUserId, unBindUser } from '@/api/sysManage/socialManage/sinaUserList'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Sort } from '@/domains/sort'
import { SinaUser } from '@/domains/user'
import { Pages } from '@/domains/pages'
import sortable from '@/utils/sortable'
import { checkSelection } from '@/utils/checkSelection'

class Query extends ListQuery {
  public nickname: string = ''
}

class Page extends Pages {
  public list: SinaUser[] = []
}

@Component({
  name: 'SinaUser',
  components: {
    TableList,
    Pagination
  },
  filters: {
    filtersSex(v: number) {
      const sexArr = ['女', '男']
      return sexArr[v]
    }
  }
})
export default class WechatUserList extends Vue {
  /** 查询条件 */
  private listQuery: Query = new Query()
  /** 数据列表 */
  private list: SinaUser[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: '用户ID',
      key: 'sinaUserId'
    },
    {
      label: '用户名',
      key: 'name'
    },
    {
      label: '性别',
      key: 'gender'
    },
    {
      label: 'id号',
      key: 'idstr'
    },
    {
      label: '位置信息',
      key: 'location'
    },
    {
      label: '主页',
      key: 'url'
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前选中的数据 */
  private multipleSelection: SinaUser[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'sinaUserId'
  created(): void {
    this.getList()
  }
  /** 获取数据列表 */
  private getList(): void {
    fetchList(this.listQuery).then((response: any) => {
      const page: Page = response.page
      this.list = page.list
      this.total = page.totalCount

      // this.newList = this.list.map((v: Role) => v.roleId)
      this.newList = this.list.map((v: SinaUser) => v[this.idKey] as string)

      const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      sortable(el, this.list, this.newList)
    })
  }
  /** 搜索 */
  private handleFilter(): void {
    this.listQuery._search = true
    this.listQuery.page = 1
    this.getList()
  }
  /** 排序 */
  private handleSortChange(sort: Sort): void {
    if (sort.sidx !== undefined) {
      this.listQuery.sidx = sort.sidx
    }
    this.listQuery.order = sort.order
    this.handleFilter()
  }
  /** 选择列表 */
  private handleSelectionChange(val: SinaUser[]): void {
    this.multipleSelection = val
  }
  /** 删除数据 */
  private handleDelete(): void {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('是否确定删除该数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids: string[] = []
        this.multipleSelection.map(item => {
          ids.push(item[this.idKey] as string)
        })
        deleteData(ids).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.handleFilter()
        })
      }).catch(() => {})
    })
  }
  // 查看关联账号
  private handleBind(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      const data = {
        wechatUserId: this.multipleSelection[0].wechatUserId
      }
      fetchUserId(data).then((response: any) => {
        if (response.userId) {
          this.$router.push({ name: 'BindUserList', query: { userId: response.userId } })
        }
      })
    })
  }
  // 解除账号绑定
  private handleUnbind(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.$confirm('是否确定解除改账号绑定?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const data = {
          wechatUserId: this.multipleSelection[0].wechatUserId
        }
        unBindUser(data).then(response => {
          this.$alert('解除绑定成功')
        })
      }).catch(() => {})
    })
  }
}
