import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'

interface IUserId {
  wechatUserId: string
}

export function fetchList(query: ListQuery) {
  return request({
    url: '/social/syssinauser',
    method: 'get',
    params: query
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/social/syssinauser',
    method: 'delete',
    data
  })
}

export function fetchUserId(query: IUserId) {
  return request({
    url: '/social/getSysUserIdThruSinaUserId',
    method: 'get',
    params: query
  })
}

export function unBindUser(query: IUserId) {
  return request({
    url: '/social/syssinauser/unbind',
    method: 'delete',
    params: query
  })
}
